"use strict";
import Cart from './cart.js';
import CartModal from './cartModal.js';
import CartUI from './cartUI.js';

function cartInit() {
    // –°–æ–∑–¥–∞–µ–º —ç–∫–∑–µ–º–ø–ª—è—Ä –∫–æ—Ä–∑–∏–Ω—ã
    const cart = new Cart();
    
    // –°–æ–∑–¥–∞–µ–º –∏ –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∏—Ä—É–µ–º –º–æ–¥—É–ª–∏
    const cartModal = new CartModal(cart);
    const cartUI = new CartUI(cart);
    
    // –î–µ–ª–∞–µ–º –¥–æ—Å—Ç—É–ø–Ω—ã–º–∏ –≥–ª–æ–±–∞–ª—å–Ω–æ (–¥–ª—è –æ–±—Ä–∞–±–æ—Ç—á–∏–∫–æ–≤ —Å–æ–±—ã—Ç–∏–π –≤ HTML)
    window.cartModal = cartModal;
    
    // –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∏—Ä—É–µ–º UI
    cartUI.initCartUI();
}

export default cartInit;